.word-correct {
  background-color: rgba(0, 191, 151, 0.55);
}

.word-wrong {
  background-color: rgba(207, 35, 0, 0.55);
}

.minigames {
  background: url("../../data/images/savannah-bg.jpg") no-repeat;
  background-position-y: bottom;
  background-position-x: center;

  &:fullscreen .minigames__wrapper {
    height: 100%;
  }

  &:fullscreen .audio-challenge__wrapper {
    min-height: 460px;
  }

  & .welcome-screen__btn {
    width: 140px;
    margin: 20px 5px 0;
  }

  &__start-game {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 50%;
    margin-top: 5%;
  }

  &__choose-type {
    font-size: 28px;;
  }

  &__panel {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 50px;
  }

  &__sound {
    color: #fff;
    font-size: 2rem;
  }

  &__fullscreen > * {
    cursor: pointer;
  }

  &__left-panel {
    display: flex;
    justify-content: space-between;
    width: 110px;
  }

  &__right-panel {
    display: flex;
    justify-content: space-between;
    width: 255px;
  }

  &__wrapper {
    position: relative;
    height: calc(100vh - 130px - 94px);
    min-height: 480px;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }

  &__translation-list {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    margin-top: 30px;
    text-align: center;
    font-size: 22px;
    font-weight: 400;
  }

  &__translation-item {
    display: inline-block;
    margin: 0 8px;
    padding: 15px 16px;
    vertical-align: top;
    color: #fff;

    &:hover {
      outline: 1px solid rgba(196, 196, 196, 0.5);
    }

    & > * {
      cursor: pointer;
    }
  }

  &__result {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

    & > div {
      width: 370px;
      height: 442px;
      background-color: #fff;
      border-radius: 5px;
    }
  }
}

@media (max-width: $middle-width) {
  .minigames {
    &__wrapper {
      min-height: 650px;
    }

    &__right-panel {
      width: 220px;
    }

    &__translation-list {
      width: 70%;
    }

    &__translation-item {
      width: 50%;
      margin: 0;
      box-sizing: border-box;
    }
  }
}

@media (max-width: $tablet-width) {
  .minigames {
    &__translation-list {
      width: 100%;
      top: 45%;
      margin-top: 0;
      z-index: 10;
    }

    &__panel {
      padding-top: 30px;
    }
  }
}
